/* -------------------------------------
		GLOBAL
------------------------------------- */
/* OBLIGATORY EMAIL STYLES SO I DON'T GO CRAZY */

p {
  @include em(margin, 0);
  @include em(padding-bottom, 10px);
  @include em(line-height, 1);
  vertical-align: middle;
}

img {
  @include em(font-size, 20px);
	display: block;
	-ms-interpolation-mode: bicubic;
	border: 0;
	line-height: 1;
	outline: none;
	text-decoration: none;
	mso-line-height-rule: exactly;
}

body, table, td, a {
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

table {
	-premailer-cellspacing: 0;
	-premailer-cellpadding: 0;
	-premailer-border: 0;
}

body {
	height: 100% !important;
	width: 100% !important;
  @include em(margin, 0);
  @include em(padding, 0);
}

/* Let's make sure all tables have defaults */
table {
	line-height: 100%;
	mso-table-lspace: 0pt;
	mso-table-rspace: 0pt;
	border-collapse: collapse !important;

	td {
		padding: 0;
		vertical-align: middle;
		border-collapse: collapse;
		mso-table-lspace: 0pt;
		mso-table-rspace: 0pt;
	}
}

td, a, span {
	mso-line-height-rule: exactly;
}

.MsoNormal p {
	padding: 0 !important;
	margin: 0;
	display: none;
}

#outlook a {
	padding: 0;
}

.ReadMsgBody, .ExternalClass {
	width: 100%;
}

.ExternalClass, .ExternalClass p,
.ExternalClass span, .ExternalClass font,
.ExternalClass td, .ExternalClass div {
	line-height: 1;
	margin: 0;
}

/* BYE BYE BLUE LINKS + ORPHAN STYLE */
.appleFooter a {
	color: #999999 !important;
	text-decoration: none !important;
}

.appleLink a {
	color: #4a4a4a !important;
	text-decoration: none !important;
}

.appleLink1 a {
	color: #4a4a4a !important;
	text-decoration: none !important;
}

.appleLink2 a {
	color: #4a4a4a !important;
	text-decoration: none !important;
}

.ExternalClass span[class="ecxoutlook-text"] {
	font-size: 13px !important;
}
