.bullet-container {

	background-color: $secondary-base;

	img[src^="/images/spacer.gif"] {

		max-width: 0.1px;
	}

	.main-wrap {
    $mainWrap: $main-max-width - 40;
    $width: (($mainWrap * 100) / $main-width)+0%;
    //change the width percentage on table
    // unit((540 * 100) / 600)+0% = 90%
    width: $width;
    max-width: $mainWrap;
		background-color: $secondary-base;

		tr:first-child {

			td {
				font-family: $font;
				font-size: 16px;
				color: $primary-color-text;
				line-height: 140%;
			}
		}

		a, p {

			font-family: $font;
			font-size: 16px;
			color: $primary-color-text;
			line-height: 140%;
		}

		span {
			color: $theme-color-text;
			font-size: 16px;
		}

		a {

			color: $theme-color-text;
			text-decoration: none;
			border-bottom: 1px solid;
		}

		.bullet-list {

			// border-left: 3px solid orange;

			tr:last-child {

				p {

					margin: 0px;
				}
			}

			tr:not(:last-child) {

				p {

					margin: 0px;
				}
			}

			a, p {

				font-family: $font;
				font-size: 16px;
				color: $primary-color-text;
				line-height: 140%;
			}

			a {

				color: $theme-color-text;
				text-decoration: none;
				border-bottom: 1px solid;
			}

			.bullet {

				font-size: 16px;
				color: $primary-color-text;
				line-height: 140%;
			}
		}
	}
}
